/* --- Basics --- */

.loading {
  display: inline-block;
  overflow: hidden;
  height: 1.3em;
  margin-top: -0.3em;
  line-height: 1.5em;
  vertical-align: text-bottom;
  font-weight: 900;
}

.loading::after {
  display: inline-table;
  white-space: pre;
  text-align: left;
}

/* --- Types --- */

/* default loading is ellip */
.loading::after {
  content: "\A.\A..\A...";
  animation: spin4 2s steps(4) infinite;
}

.loading.line::after {
  content: "/\A–\A\\\A|";
  text-align: center;
  animation: spin4 1s steps(4) infinite;
}

.loading.line2::after {
  content: "╲\A│\A╱\A─";
  text-align: center;
  animation: spin4 1s steps(4) infinite;
}

.loading.plus::after {
  content: "┽\A╀\A┾\A╁";
  animation: spin4 1s steps(4) infinite;
}

.loading.dots::after {
  content: "⠋\A⠙\A⠹\A⠸\A⠼\A⠴\A⠦\A⠧\A⠇\A⠏";
  animation: spin10 1s steps(10) infinite;
}

.loading.dots2::after {
  content: "⠋\A⠙\A⠚\A⠞\A⠖\A⠦\A⠴\A⠲\A⠳";
  animation: spin9 1s steps(9) infinite;
}

.loading.dots3::after {
  content: "⋮\A⋰\A⋯\A⋱";
  text-align: center;
  animation: spin4 1s steps(4) infinite;
}

.loading.lifting::after {
  content: "꜈꜍\A꜉꜎\A꜊꜏\A꜋꜐\A꜌꜑";
  animation: spin5 0.5s steps(5) infinite alternate;
}

.loading.hamburger::after {
  content: "☱\A☲\A☴";
  animation: spin3 0.3s steps(3) infinite alternate;
}

.loading.bar::after {
  content: "▏\A▎\A▍\A▌\A▋\A▊\A▉";
  animation: spin7 1s steps(7) infinite alternate;
}

.loading.bar2::after {
  content: "▁\A▂\A▃\A▄\A▅\A▆\A▇\A█";
  animation: spin8 2s steps(8) infinite alternate;
}

.loading.circle::after {
  content: "◴\A◷\A◶\A◵";
  animation: spin4 1s steps(4) infinite;
}

.loading.open-circle::after {
  content: "◜\A◠\A◝\A◞\A◡\A◟";
  animation: spin6 0.6s steps(6) infinite;
}

.loading.arrow::after {
  content: "←\A↖\A↑\A↗\A→\A↘\A↓\A↙";
  animation: spin8 1s steps(8) infinite;
}

.loading.triangle::after {
  content: "◢\A◣\A◤\A◥";
  animation: spin4 1s steps(4) infinite;
}

.loading.triangles::after {
  content: "▹▹▹▹▹\A ▸▹▹▹▹\A ▹▸▹▹▹\A ▹▹▸▹▹\A ▹▹▹▸▹\A ▹▹▹▹▸";
  animation: spin6 1s steps(6) infinite;
}

.loading.beam::after {
  content: "\A=   \A ==  \A === \A ====\A  ===\A   ==\A    =\A";
  animation: spin9 1.2s steps(9) infinite;
  font-family: monospace;
}

.loading.bullet::after {
  content: " ●    \A   ●   \A    ●  \A     ● \A      ●\A     ● \A    ●  \A   ●   \A  ●    \A ●     ";
